<template>
	<div v-bind="$attrs"></div>
</template>

<script>
import EasterEggTriggerCore from '../plugin/index';

export default {
	name: 'EasterEggComponent',
	props: {
		callback: {
			default: null,
			type: Function,
		},
		pattern: {
			default: () => ['ArrowUp', 'ArrowUp', 'ArrowDown', 'ArrowDown', 'ArrowLeft', 'ArrowRight', 'ArrowLeft', 'ArrowRight', 'b', 'a'],
			type: Array,
		},
		target: {
			default: 'body',
			type: String,
		},
		triggered: {
			default: null,
			type: Function,
		},
		type: {
			default: 'keydown',
			type: String,
		},
	},
	data: () => ({
		defaultOptions: {},
	}),
	computed: {},
	watch: {},
	created() {
		this.defaultOptions = EasterEggTriggerCore.defaultOptions;
		console.log('EasterEggComponent created');
	},
	mounted() {
		EasterEggTriggerCore.init(this.defaultOptions, this.$attrs);
	},
};
</script>
